*{margin:0;padding:0;}
:root {
  --first-color: #2c74b8;
  --second-color: #d37522;
  --third-color: #807f7d;
  --fullhd-size:1920px;
  --table-size:768px;
  --mobile-size:500px;
}

body{margin:0;padding: 0;font-family: 'Oswald', sans-serif;overflow-x:hidden;background: #e9e9e9;}
#maximusContainer{width:100%;height:100%;position: fixed;position: fixed;left: 0;top: 0;overflow: hidden;}
#scrollContainer{width: 100%;min-height: 100vh;padding-bottom: 60px;}
#maximusContainer.mobile{width:100%;height:100%;position: relative;overflow: unset;}
#scrollContainer.mobile{padding-bottom: 0;}


.container{width:90%;margin:0 auto;max-width:1920px;}
#canvasBackground{position: fixed;top:0;left:0;min-height: 100vh;width:100%;z-index: -1;}
header input{display: none;}
header input + label{position: absolute;z-index: 5;top:10px;right:10px;}
