#nav-icon2.open svg:nth-child(3){opacity: 0;display: none;}

nav{
  display: grid;align-items: center;
  position: absolute;top:0;z-index: 1;
  width: 100%;height: 100vh;background: #fff;
  -webkit-transform: translate(200vw);
  -moz-transform: translate(200vw);
  -o-transform: translate(200vw);
  transform: translate(200vw);

  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;

  -webkit-animation:closeMenu 1s;
  -moz-animation:closeMenu 1s;
  -ms-animation:closeMenu 1s;
  animation:closeMenu 1s;  
}
nav.open{
  -webkit-transform: translate(0);
  -moz-transform: translate(0);
  -o-transform: translate(0);
  transform: translate(0);
  -webkit-animation:openMenu 1s;
  -moz-animation:openMenu 1s;
  -ms-animation:openMenu 1s;
  animation:openMenu 1s;   
}
nav ul{padding:0;margin:0;width:100%;height:50vh;list-style: none;text-align: center;display: grid;align-items:center;grid-template-rows: repeat(5,1fr);}
nav ul li{overflow:hidden;}
nav ul li a{color:var(--first-color);text-decoration: none;font-size: 2em;overflow:hidden;}
nav ul li a.active{color:#c7c7c7;}
nav ul li a span{display:block;opacity:0;transform:translate3d(0, -80%,0px);
  -webkit-transition: .25s ease-in-out;-moz-transition: .25s ease-in-out;-o-transition: .25s ease-in-out;transition: .25s ease-in-out;
  -webkit-animation:closeNavLink 1s;
  -moz-animation:closeNavLink 1s;
  -ms-animation:closeNavLink 1s;
  animation:closeNavLink 1s;
}
nav ul li:hover a span{color:#c7c7c7;}
nav ul.open li a span{opacity:1;transform:translate(0px,0px);
  -webkit-animation:openNavLink 2s;
  -moz-animation:openNavLink 2s;
  -ms-animation:openNavLink 2s;
  animation:openNavLink 2s;
}


/*    FOOTER      */
footer{height:50vh;background:#0f0f0f;color: var(--third-color);display:grid;grid-template-columns:repeat(3,1fr);}
footer p {margin:0;}


/*  ANIMATIONS  */

@-webkit-keyframes openMenu{from{opacity:0;}to{opacity:1;}}
@-moz-keyframes openMenu{from{opacity:0;}to{opacity:1;}}
@-ms-keyframes openMenu{from{opacity:0;}to{opacity:1;}}
@-o-keyframes openMenu{from{opacity:0;}to{opacity:1;}}
@keyframes openMenu{from{opacity:0;}to{opacity:1;}}

@-webkit-keyframes closeMenu{from{opacity:1;}to{opacity:0;}}
@-moz-keyframes closeMenu{from{opacity:1;}to{opacity:0;}}
@-ms-keyframes closeMenu{from{opacity:1;}to{opacity:0;}}
@-o-keyframes closeMenu{from{opacity:1;}to{opacity:0;}}
@keyframes closeMenu{from{opacity:1;}to{opacity:0;}}

@-webkit-keyframes openNavLink{from{opacity:0;transform: translate3d(0, -80%,0px);}to{opacity:1;transform:translate(0px,0px)}}
@-moz-keyframes openNavLink{from{opacity:0;transform: translate3d(0, -80%,0px);}to{opacity:1;transform:translate(0px,0px)}}
@-ms-keyframes openNavLink{from{opacity:0;transform: translate3d(0, -80%,0px);}to{opacity:1;transform:translate(0px,0px)}}
@-o-keyframes openNavLink{from{opacity:0;transform: translate3d(0, -80%,0px);}to{opacity:1;transform:translate(0px,0px)}}
@keyframes openNavLink{from{opacity:0;transform: translate3d(0, -80%,0px);}to{opacity:1;transform:translate(0px,0px)}}

@-webkit-keyframes closeNavLink{from{opacity:1;transform:translate(0px,0px);}to{opacity:0;transform: translate3d(0, -80%,0px);}}
@-moz-keyframes closeNavLink{from{opacity:1;transform:translate(0px,0px);}to{opacity:0;transform: translate3d(0, -80%,0px);}}
@-ms-keyframes closeNavLink{from{opacity:1;transform:translate(0px,0px);}to{opacity:0;transform: translate3d(0, -80%,0px);}}
@-o-keyframes closeNavLink{from{opacity:1;transform:translate(0px,0px);}to{opacity:0;transform: translate3d(0, -80%,0px);}}
@keyframes closeNavLink{from{opacity:1;transform:translate(0px,0px);}to{opacity:0;transform: translate3d(0, -80%,0px);}}

/*    THEME   */
.sectionFirst{width: 100%;height:100vh;}
.revealOnScroll{overflow:hidden;}

/*    home    */
#firstSectionHome .container{display: grid;grid-template-rows: 100px 30% 50%;padding:20vh 0 0 10%;height:calc(100vh - 20vh); }
#firstSectionHomeText{overflow:hidden;color:#807f7d;padding: 0;max-width: 70%;font-size: 1.5em;margin: 0;font-weight: 100;}
#firstSectionHomeText span{display:block;}
#logoHome{max-width: 500px;display: grid;align-self: center;width:90%;}
.videoContainer{width: 500px;margin:0 0 0 calc(100% - 500px);}
.videoContainer video{width:100%;box-shadow: 5px 2px 10px rgba(0, 0, 0, 0.5);}
@media(max-width: 500px){
  #firstSectionHome .container{height: 50vh;}
  #firstSectionHomeText{font-size: 1em;}

  .videoContainer{width:320px;margin:0 0 0 calc(100% - 320px);}
}

@media(max-width: 360px){
  #firstSectionHome .container{height: 80vh;}
}

/*    CAROUSEL    */
.sectionCarousel{padding:50px 0;}
@media(max-width: 500px){.sectionCarousel{padding:0 0 50px 0;}}
.titlesectionCarousel{text-align: center;font-size: 1.5em;max-width: 340px;margin: 0 auto 50px auto;}
.glide {
    &__slide {min-height: 50vh;}
    .box{max-width:360px;height: 100%;text-align:center;margin:0 auto;padding:20px 30px;background: #fff;border-radius: 5px;color:var(--third-color);}
    .box{
      .profilePicture{display:block;width:100px;margin:0 auto;opacity: 0;
        -webkit-transform: translate(0,-10px);-moz-transform: translate(0,-10px);-o-transform: translate(0,-10px);transform: translate(0,-10px);
        -webkit-transition: 1s ease-in-out;-moz-transition: 1s ease-in-out;-o-transition: 1s ease-in-out;transition: 1s ease-in-out;}
      .memberTitle{text-align: center;color:var(--first-color); margin:15px 0;font-size: 1.8em;opacity:0;
        -webkit-transform: translate(0,10px);-moz-transform: translate(0,10px);-o-transform: translate(0,10px);transform: translate(0,10px);
        -webkit-transition: 1s ease-in-out;-moz-transition: 1s ease-in-out;-o-transition: .1s ease-in-out;transition: 1s ease-in-out;}
      .memberDescription{opacity:0;margin:10px 0;font-weight:100;
        -webkit-transform: translate(0,10px);-moz-transform: translate(0,10px);-o-transform: translate(0,10px);transform: translate(0,10px);
        -webkit-transition: 1s ease-in-out;-moz-transition: 1s ease-in-out;-o-transition: 1s ease-in-out;transition: 1s ease-in-out;}
      .socialContainer{margin:25px 0;opacity: 0;-webkit-transition: 1s ease-in-out;-moz-transition: 1s ease-in-out;-o-transition: 1s ease-in-out;transition: 1s ease-in-out;}
      .socialContainer img{width: 30px;}
    }
    /*&__slide--active .box{*/
    &__slide .box{
      .profilePicture{opacity: 1;
        -webkit-transform: translate(0);-moz-transform: translate(0);-o-transform: translate(0);transform: translate(0);
      }
      .memberTitle{opacity: 1;
        -webkit-transform: translate(0);-moz-transform: translate(0);-o-transform: translate(0);transform: translate(0);
      }
      .memberDescription{opacity: 1;
        -webkit-transform: translate(0);-moz-transform: translate(0);-o-transform: translate(0);transform: translate(0);
      }
      .socialContainer{opacity: 1;}
    }
}


@media(max-width: 500px){
  .glide {
    .box{max-width: unset;}
  }
}